import { Button, Radio, Select, Space, Typography } from 'antd';
import { useDispatch, useSelector } from 'react-redux';
import { updateSelected, updatemCi, toggleScanStatus, updateMaxCount, clear, updateScanMode, updateScanTime, addProgressedTime } from './modules/scan';
import { DecimalStepSlider, IntegerStepSlider } from './component/customSlider';
import Scan from './component/scan';
import { COUNT, SCAN_TIME, COUNT_AND_SCAN_TIME } from './utils/constants';
import { useEffect } from 'react';
import { stopCountFormatter, scanTimeFormatter } from './utils/formatter';

function App() {
  const dispatch = useDispatch()
  const folderList = useSelector(state => state.scan?.folderList || [])
  const selectedFolder = useSelector(state => state.scan?.selected)
  const scanStatus = useSelector(state => !!state.scan?.scanning)
  const maxCount = useSelector(state => state.scan?.maxCount)
  const scanTime = useSelector(state => state.scan?.scanTime)
  const progressedTime = useSelector(state => state.scan?.progressedTime)
  const mCi = useSelector(state => state.scan?.mCi)
  const mode = useSelector(state => state.scan?.mode)
  
  const onSelectChange = e => dispatch(updateSelected(e))
  const onScanBtnClick = () => dispatch(toggleScanStatus())
  const onClear = () => dispatch(clear())

  const onRadioactivityChange = value => dispatch(updatemCi(value))
  const mCiFormatter = value => `${value} mCi`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')

  const onRadioButtonChange = e => dispatch(updateScanMode(e.target.value))

  
  const radioOptions = [COUNT, SCAN_TIME, COUNT_AND_SCAN_TIME].map(value => ({label: value, value}))

  const onMaxCountChange = value => dispatch(updateMaxCount(value))
  const onScanTimeChange = value => {
    if (value >= progressedTime) {
      dispatch(updateScanTime(value))
    }
  }

  const modeSliderOptions = {
    [COUNT]: [{
      title: 'Max Count',
      onChange: onMaxCountChange,
      min: 0,
      max: 100000,
      value: maxCount,
      formatter: stopCountFormatter,
      disabled: scanStatus
    }],
    [SCAN_TIME]: [{
      title: 'Scan Time',
      onChange: onScanTimeChange,
      min: 0,
      max: 1800,
      value: scanTime,
      formatter: scanTimeFormatter,
      disabled: scanStatus
    }]
  }
  modeSliderOptions[COUNT_AND_SCAN_TIME] = [COUNT, SCAN_TIME].map(value => modeSliderOptions[value][0])

  useEffect(() => {
    let interval
    if ((mode === SCAN_TIME || mode === COUNT_AND_SCAN_TIME) && scanStatus) {
      interval = setInterval(() => {
        dispatch(addProgressedTime(1))
      }, 1000)
    }
    return () => clearInterval(interval)
  },[scanStatus, mode])
  
  return (
    <div style={{margin: '5px'}}>
      <div>
        <Select
          value={selectedFolder}
          style={{width: '200px'}}
          options={folderList.map(folder => ({
            value: folder.name,
            label: folder.name
          }))}
          onChange={onSelectChange}
          disabled={scanStatus}
        />
        <Button onClick={onScanBtnClick} disabled={!selectedFolder}>{scanStatus? "Stop Scan" : "Start Scan"}</Button>
        <Space>
          <Button onClick={onClear} disabled={scanStatus}>Clear</Button>
        </Space>
      </div>
      <div>
        <Space style={{display: 'block'}}>
          <Typography.Text>Radioactivity</Typography.Text>
          <DecimalStepSlider 
            min={0.5}
            max={20}
            step={0.01}
            onChange={onRadioactivityChange}
            disabled={scanStatus}
            value={mCi}
            formatter={mCiFormatter}
          />
        </Space>
        <Space style={{display: 'block'}}>
          <Radio.Group 
            options={radioOptions} 
            onChange={onRadioButtonChange} 
            value={mode} 
            optionType='button'
            buttonStyle='solid'
            disabled={scanStatus}
          />
          {
            modeSliderOptions[mode].map(option => {
              return (
                <>
                  <Typography.Text>{option.title}</Typography.Text>
                  <IntegerStepSlider {...option} />
                </>
              )
            })
          }
          

        </Space>
      </div>
      <Space>
        {
          selectedFolder
          ? folderList.find(folder => folder.name === selectedFolder).list
            .map((imageInfo, index) => <Scan key={index} imageName={imageInfo.name}/>)
          : null
        }
      </Space>
    </div>
  );
}

export default App;
