export function arrayToBase64(array) {
  if (array.length === 0) {
    return
  }
  const canvas = document.createElement('canvas');
  canvas.width = array[0].length;
  canvas.height = array.length;

  const ctx = canvas.getContext('2d');
  const imageData = ctx.createImageData(canvas.width, canvas.height);

  for (let i = 0; i < array.length; i++) {
    for (let j = 0; j < array[i].length; j++) {
      const value = array[i][j]
      const [r, g, b, a=255] = value
      imageData.data[(i * canvas.width + j) * 4] = r; // R
      imageData.data[(i * canvas.width + j) * 4 + 1] = g; // G
      imageData.data[(i * canvas.width + j) * 4 + 2] = b; // B
      imageData.data[(i * canvas.width + j) * 4 + 3] = a; // Alpha 0 ~ 255
    }
  }

  ctx.putImageData(imageData, 0, 0);
  return canvas.toDataURL();
}