import { Image as AntImage } from "antd"
import { useEffect, useRef, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { PUBLIC_IMAGE_PATH } from "../utils/constants"
import { arrayToBase64 } from "../util"
import { toggleScanStatus, updateWindowWidth, updateWindowLevel, slideWindow } from "../modules/scan"
import { COUNT, SCAN_TIME, COUNT_AND_SCAN_TIME } from "../utils/constants"
import { stopCountFormatter, scanTimeFormatter } from "../utils/formatter"

function Scan({imageName}) {
  const dispatch = useDispatch()
  const folderList = useSelector(state => state.scan.folderList)
  const selectedFolder = useSelector(state => state.scan?.selected)
  const scanStatus = useSelector(state => !!state.scan?.scanning)
  const maxCount = useSelector(state => state.scan?.maxCount)
  const scanTime = useSelector(state => state.scan?.scanTime)
  const progressedTime = useSelector(state => state.scan?.progressedTime)

  const imageInfo = folderList.find(folder => folder.name === selectedFolder)
  const {width=256, height=256} = imageInfo
  const imagePath = `${PUBLIC_IMAGE_PATH}/${selectedFolder}/${imageName}`
  const dummyArray = useRef([])
  const ratioMask = useRef([]) // save pixel ratio
  const actualValueArray = useRef([]) // save actual value
  const [viewArray, setViewArray] = useState([])
  const iterRef = useRef(0)
  const minMaxSum = useRef([0, 0, 0])
  const [sumCount, setSumCount] = useState(0)

  const maxLocaton = useRef(['', ''])

  const mCi = useSelector(state => state.scan?.mCi)
  const mode = useSelector(state => state.scan?.mode)

  const [locateValue, setLocateValue] = useState(0)
  const clickLocate = useRef([0,0])

  const window = useSelector(state => state.scan?.window)
  const windowWidth = window.width
  const windowLevel = window.level

  const [mouseClick, setMouseClick] = useState(false)
  const clickLocation = useRef({x:0, y:0})

  useEffect(() => {
    console.log(windowWidth, windowLevel);
  },[windowWidth, windowLevel])

  useEffect(()=>{
    initialize()
  },[])

  const initialize = () => {
    const result = new Array(height);
    const zeroResult = new Array(height);
    for (let y = 0; y < height; y++) {
      result[y] = new Array(width);
      zeroResult[y] = new Array(width);
      for (let x = 0; x < width; x++) {
        result[y][x] = [255, 255, 255, 255]
        zeroResult[y][x] = 0
      }
    }
    dummyArray.current = result
    ratioMask.current = result
    actualValueArray.current = zeroResult
    minMaxSum.current = [0, 0, 0]
    setViewArray(result)
    setLocateValue(0)
    clickLocate.current = [0,0]
  }

  useEffect(()=>{
    initialize()
    makeRatioMask(imagePath)
  },[imagePath])

  function makeRatioMask(image) {
    const canvas = document.createElement('canvas');
    canvas.width = width
    canvas.height = height
  
    const ctx = canvas.getContext('2d');
    const myImage = new Image()
    myImage.src = image
    myImage.onload = () => {
      if (width === 0 || height === 0) {
        return 
      }
      ctx.drawImage(myImage, 0, 0, width, height)
      const imageData = ctx.getImageData(0, 0, width, height).data;
      const ratio = new Array(height);
      for (let y = 0; y < height; y++) {
        ratio[y] = new Array(width);
        for (let x = 0; x < width; x++) {
          const index = (y * width + x) * 4;
          const value = 255 - imageData[index];
          ratio[y][x] = value/255
        }
      }
      ratioMask.current = ratio
      return ratio
    }
  }

  function startScan(scanTime, progressedTime) {
    let [min, max, sum] = [...minMaxSum.current]
    
    if (!scanStatus) {
      return clearInterval(timerId.current)
    }
    if (mode === COUNT && maxCount < max) {
      clearInterval(timerId.current)
      return dispatch(toggleScanStatus())
    }
    if (mode === SCAN_TIME && scanTime <= progressedTime) {
      clearInterval(timerId.current)
      return dispatch(toggleScanStatus())
    }
    if (mode === COUNT_AND_SCAN_TIME && (maxCount < max || scanTime <= progressedTime)) {
      clearInterval(timerId.current)
      return dispatch(toggleScanStatus())
    }
    // process actual value
    const result = new Array(height);
    [min, max, sum] = [255, 0, 0]
    for (let y = 0; y < height; y++) {
      result[y] = new Array(width);
      for (let x = 0; x < width; x++) {
        const ratio = ratioMask.current[y][x]
        let value = 0
        const randomValue = Math.random()
        if (mCi < 1) {
          value = randomValue < (ratio * mCi) ? 1 : 0;
        } else {
          value = randomValue < (ratio) ? Math.round(randomValue * mCi) : 0;
        }
        
        actualValueArray.current[y][x] += value
        const currValue = actualValueArray.current[y][x]
        sum += currValue
        
        if (min > currValue) {
          min = currValue
        }
        if (max < currValue) {
          max = currValue
          maxLocaton.current = [x, y]
        }
      }
    }
    // process viewing array
    minMaxSum.current = [min, max, sum]
    getProcessedArray()
    iterRef.current +=1
    setSumCount(sum)
  }

  const getProcessedArray = (windowWidth, windowLevel) => {
    const [min, max, _] = minMaxSum.current
    let processedArray = [...dummyArray.current]
    for (let y = 0; y < height; y++) {
      for (let x = 0; x < width; x++) {
        const currValue = actualValueArray.current[y][x]
        let minMaxValue
        if (windowWidth !== undefined && windowLevel !== undefined) {
          const windowingMin = Math.floor(windowLevel - (windowWidth / 2))
          const windowingMax = Math.ceil(windowLevel + (windowWidth / 2))
          if (currValue <= windowingMin){
            minMaxValue = 255
          }
          if (currValue >= windowingMax){
            minMaxValue = 0
          }
          else {
            minMaxValue = 255 - Math.ceil((currValue - windowingMin)/(windowingMax - windowingMin) * 255)
          }
        } 
        else {
          minMaxValue = 255 - Math.ceil((currValue - min)/(max - min) * 255)
        }
        processedArray[y][x] = [minMaxValue, minMaxValue, minMaxValue, 255]
        const [locateX, locateY] = [...clickLocate.current]
        if (locateX === x && locateY === y) {
          setLocateValue(currValue)
        }
      }
    }
    setViewArray(processedArray)
  }

  useEffect(() => {
    if (windowLevel !== undefined && windowWidth !== undefined) {
      getProcessedArray(windowWidth, windowLevel)
    }
  },[windowLevel, windowWidth])

  const timerId = useRef();

  useEffect(()=>{
    if (scanStatus) {
      dispatch(updateWindowWidth(undefined))
      dispatch(updateWindowLevel(undefined))
      timerId.current = setInterval(() => startScan(scanTime, progressedTime), 10)
    } else {
      clearInterval(timerId.current)
      const [min, max, _] = minMaxSum.current
      if (max !== 0) {
        dispatch(updateWindowWidth(max - min))
        dispatch(updateWindowLevel(Math.ceil((max + min)/2)))
      }
    }
    return () => clearInterval(timerId.current)
  },[scanStatus, scanTime, progressedTime])

  const handleClick = event => {
    const x = event.nativeEvent.offsetX;
    const y = event.nativeEvent.offsetY;
    const value = actualValueArray.current[y][x]
    clickLocate.current = [x, y]
    setLocateValue(value)
  };

  const locateInfo = `Locate info: (${clickLocate.current?.[0] || ' '}, ${clickLocate.current?.[1] || ' '})  value: ${stopCountFormatter(locateValue)|| 0}`
  
  const stopCondition = () => {
    let conditionInfo = []
    if (mode === COUNT || mode === COUNT_AND_SCAN_TIME) {
      const [x, y] = maxLocaton.current
      const max = [...minMaxSum.current]?.[1]
      conditionInfo.push(`Max info: (${x}, ${y}) ${stopCountFormatter(max)}`)
    }
    if (mode === SCAN_TIME || mode === COUNT_AND_SCAN_TIME) {
      const remainTime = (scanTime - progressedTime) < 0 ? 0 : scanTime - progressedTime
      conditionInfo.push(`Remain time: ${scanTimeFormatter(remainTime)}/${scanTimeFormatter(scanTime)}`)
    }
    return conditionInfo.map(info => <p>{info}</p>)
  }

  const handleMouseDown = e => {
    if (!scanStatus) {
      setMouseClick(true)
      clickLocation.current = { x: e.clientX, y: e.clientY }
    }
  };

  const handleMouseUp = () => {
    if (!scanStatus) {
      setMouseClick(false)
    }
  };


  const handleMouseMove = e => {
    if (mouseClick && !scanStatus) {
      const [min, max, _] = minMaxSum.current
      const diffX = e.clientX - clickLocation.current.x
      const diffY = e.clientY - clickLocation.current.y
      dispatch(updateWindowWidth(max - diffX))
      dispatch(updateWindowLevel(Math.ceil((max + min)/2 - diffY)))
      // dispatch(slideWindow({ x: Math.ceil(diffX/10), y: Math.ceil(diffY/10) }))
    }
  };

  return(
    <>
      {
        viewArray.length > 0
        ? (
          <>
            <AntImage
              width={width}
              height={height}
              src={`${arrayToBase64(viewArray)}`} 
              onClick={handleClick}
              preview={false}
              style={{border: 'solid black 1px'}}
              draggable={false}
              onMouseDown={handleMouseDown}
              onMouseUp={handleMouseUp}
              onMouseMove={handleMouseMove}
            />
            <div>
              Total count: {sumCount.toLocaleString()}
            </div>
            <div>
              {locateInfo}
            </div>
            <div>
              {stopCondition()}
            </div>
          </>
          )
        : null
      }
    </>
  )
}

export default Scan