import { Col, InputNumber, Row, Slider } from "antd";

export const DecimalStepSlider = ({onChange, disabled, value, min, max, formatter, step}) => {
  return (
    <Row>
      <Col span={12}>
        <Slider
          min={min}
          max={max}
          onChange={onChange}
          disabled={disabled}
          value={value}
          step={step}
        />
      </Col>
      <Col span={4}>
        <InputNumber
          min={min}
          max={max}
          style={{
            margin: '0 16px',
          }}
          step={step}
          value={value}
          onChange={onChange}
          disabled={disabled}
          formatter={formatter}
        />
      </Col>
    </Row>
  );
};

export const IntegerStepSlider = ({onChange, disabled, min, max, value, formatter}) => {
  return (
    <Row>
      <Col span={12}>
        <Slider
          min={min}
          max={max}
          disabled={disabled}
          onChange={onChange}
          value={value}
          tooltip={{formatter}}
        />
      </Col>
      <Col span={4}>
        <InputNumber
          min={min}
          max={max}
          style={{
            margin: '0 16px',
          }}
          value={value}
          formatter={formatter}
          disabled={disabled}
          onChange={onChange}
        />
      </Col>
    </Row>
  );
};