import folderJson from '../utils/folders.json'

const UPDATE_FOLDER_LIST = 'scan/UPDATE_FOLDER_LIST'
const UPDATE_SELECTED = 'scan/UPDATE_SELECTED'
const CLEAR = 'scan/CLEAR'
const UPDATE_mCi = 'scan/UPDATE_mCi'
const TOGGLE_SCAN_STATUS = 'scan/TOGGLE_SCAN_STATUS'
const UPDATE_MAX_COUNT = 'scan/UPDATE_MAX_COUNT'
const UPDATE_SCAN_TIME = 'scan/UPDATE_SCAN_TIME'
const UPDATE_SCAN_MODE = 'scan/UPDATE_SCAN_MODE'
const ADD_PROGRESSED_TIME = 'scan/ADD_PROGRESSED_TIME'
const UPDATE_WINDOW_WIDTH = 'scan/UPDATE_WINDOW_WIDTH'
const UPDATE_WINDOW_LEVEL = 'scan/UPDATE_WINDOW_LEVEL'
const SLIDE_WINDOW = 'scan/SLIDE_WINDOW'

const initialState = {
  folderList: folderJson.folders,
  mCi: 0.01,
  maxCount: 5000,
  scanTime: 60,
  progressedTime: 0,
  mode: 'count',
  window: {
    width: undefined,
    level: undefined
  }
}

const reducer = (state=initialState, aciton) => {
  switch (aciton.type) {
    case UPDATE_FOLDER_LIST:
      return {
        ...state,
        folders: aciton.payload
      }
    case UPDATE_SELECTED:
      return {
        ...state,
        selected: aciton.payload
      }
    case CLEAR:
      return initialState
    case UPDATE_mCi:
      return {
        ...state,
        mCi: aciton.payload
      }
    case UPDATE_SCAN_MODE:
      return {
        ...state,
        mode: aciton.payload
      }
    case UPDATE_MAX_COUNT:
      return {
        ...state,
        maxCount: aciton.payload
      }
    case UPDATE_SCAN_TIME:
      return {
        ...state,
        scanTime: aciton.payload
      }
    case ADD_PROGRESSED_TIME:
      return {
        ...state,
        progressedTime: state.progressedTime + aciton.payload
      }
    case TOGGLE_SCAN_STATUS:
      return {
        ...state,
        scanning: !state?.scanning
      }
    case UPDATE_WINDOW_WIDTH:
      return {
        ...state,
        window: {
          ...state.window,
          width: aciton.payload
        }
      }
    case UPDATE_WINDOW_LEVEL:
      return {
        ...state,
        window: {
          ...state.window,
          level: aciton.payload
        }
      }
    case SLIDE_WINDOW:
      return {
        ...state,
        window: {
          ...state.window,
          width: state.window.width - aciton.payload.x,
          level: state.window.level - aciton.payload.y,
        }
      }
    default:
      return state
  }
}

export default reducer

export function updateFolderList(payload) {
  return {type: UPDATE_FOLDER_LIST, payload}
}

export function updateSelected(payload) {
  return {type: UPDATE_SELECTED, payload}
}

export function updatemCi(payload) {
  return {type: UPDATE_mCi, payload}
}

export function updateMaxCount(payload) {
  return {type: UPDATE_MAX_COUNT, payload}
}

export function toggleScanStatus() {
  return {type: TOGGLE_SCAN_STATUS}
}

export function clear() {
  return {type: CLEAR}
}

export function updateScanMode(payload) {
  return {type: UPDATE_SCAN_MODE, payload}
}

export function updateScanTime(payload) {
  return {type: UPDATE_SCAN_TIME, payload}
}

export function addProgressedTime(payload) {
  return {type: ADD_PROGRESSED_TIME, payload}
}

export function updateWindowWidth(payload) {
  return {type: UPDATE_WINDOW_WIDTH, payload}
}

export function updateWindowLevel(payload) {
  return {type: UPDATE_WINDOW_LEVEL, payload}
}

export function slideWindow(payload) {
  return {type: SLIDE_WINDOW, payload}
}